import Vue from 'vue'
import App from './App.vue'
import VueRouter from "vue-router";
import VueResource from 'vue-resource'

//开启debug模式
Vue.config.debug = true;

Vue.use(VueRouter);
Vue.use(VueResource);

import Index from './subpage/index.vue';
import menuone from './subpage/menuone.vue';
import notfound from './subpage/notfound.vue';
import onesub from './subpage/subone.vue';
const router = new VueRouter({
	base: __dirname,
	routes: [{
		path: '/',
		redirect: "/index",
		children: []
	}, {
		path: '/index',
		component: Index
	}, {
		path: '/one',
		component: menuone,
		children: []
	}, {
		path: "/onesub",
		component: onesub,
	}, {
		path: "*",
		component: notfound
	}]
});
let [routerHistory, appVue] = [
	[]
];
/*
 router.beforeEach注册一个全局的before钩子，
 to:Rounte:即将要进入的目标路由对象
 from：Rounte：当前导航正要离开的路由
 next：Function：一定要调用该方法来resolve这个钩子。执行效果依赖next方法的调用参数。
  。next（）：进行管道中的下一个钩子，如果全部钩子执行完啦，则导航的状态就是cnfirmed（确认的）。
  。next（false）：中断当前的导航。如果浏览器的url改变了（可能是用户手动或者浏览器后台按钮），那么url地址会重置到from路由对应的地址。
  。next（'/')或者next({path:'/'})：跳转到一个不同的地址。当前导航被中断，然后进行一个新的导航。
  确保要调用next方法，否则钩子不会被resolved。
  afterEach，after钩子没有next方法，不能改变导航。
 * */
router.beforeEach((to, from, next) => {
	let index = routerHistory.indexOf(to.fullPath)
	// if (to.path === '/') {
	//   next('/TabHome')
	//   return false
	// }
	// if (to.path === '/') {
	//   routerHistory = []
	// }
	if(index !== -1) {
		routerHistory = routerHistory.slice(0, index + 1)
		console.log('后')
		appVue.tName = 'slide-prev'
	} else {
		routerHistory.push(to.fullPath)
		console.log('前进')
		if(appVue) {
			appVue.tName = 'slide-next'
		}
	}
	global.routerHistory = routerHistory
	console.log(routerHistory)
	next()
});
appVue = new Vue({
	el: '#app',
	router,
	data: {
		tName: 'slide-next',
		text: "123",
	},
	template: '<App v-bind:tName="tName" v-bind:text="text"/>',
	components: { App },
});


