<template>
	<div id="app">
		<transition :name="tName">
			<keep-alive>
				<router-view></router-view>
			</keep-alive>
		</transition>
	</div>
</template>

<script>
	export default {
		name: 'app',
		props: ['tName', 'text'],
		data() {
			return {

			}
		},
		mounted: function() {}
	}
</script>

<style lang="scss">
	@import "static/style/base.css";
	@mixin prefix($property, $value) {
		-webkit-#{$property}: $value;
		-moz-#{$property}: $value;
		#{$property}: $value;
	}
	
	$slideDuration: 0.3s;
	/* Enter and leave animations can use different */
	/* durations and timing functions.              */
	
	.page {
		position: absolute;
		width: 100%;
		min-height: 100%;
		@include prefix(transition, all $slideDuration cubic-bezier(1.0, 0.5, 0.8, 1.0)) max-height: 100%
	}
	
	.page .page-content {
		-webkit-overflow-scrolling: touch;
		overflow: scroll;
	}
	
	.slide-next-enter-active {
		@include prefix(transition, all $slideDuration cubic-bezier(1.0, 0.5, 0.8, 1.0)) max-height: 100%;
		overflow: hidden;
		/*transition: all 1s ease;*/
		/*background-color: green;*/
	}
	
	.slide-next-leave-active {
		@include prefix(transition, all $slideDuration cubic-bezier(1.0, 0.5, 0.8, 1.0)) max-height: 100%;
		overflow: hidden;
		/*background-color: red;*/
	}
	
	.slide-next-enter
	/* .slide-fade-leave-active for <2.1.8 */
	
	{
		@include prefix(transform, translateX(100%)) max-height: 100%;
		overflow: hidden;
		opacity: 0;
	}
	
	.slide-next-leave-to {
		@include prefix(transform, translateX(-100%)) opacity: 0;
		max-height: 100%;
		overflow: hidden;
	}
	
	.slide-prev-enter-active {
		@include prefix(transition, all $slideDuration cubic-bezier(1.0, 0.5, 0.8, 1.0)) max-height: 100%;
		overflow: hidden;
		/*transition: all 1s ease;*/
		/*background-color: green;*/
	}
	
	.slide-prev-leave-active {
		@include prefix(transition, all $slideDuration cubic-bezier(1.0, 0.5, 0.8, 1.0)) max-height: 100%;
		overflow: hidden;
		/*background-color: red;*/
	}
	
	.slide-prev-enter
	/* .slide-fade-leave-active for <2.1.8 */
	
	{
		@include prefix(transform, translateX(-100%)) opacity: 0;
		max-height: 100%;
		overflow: hidden;
	}
	
	.slide-prev-leave-to {
		@include prefix(transform, translateX(100%)) opacity: 0;
	}
</style>


