<template>
	<div id="index" class="page">
		<swiper :tabs="tabs"></swiper>
		{{a}}
		<div v-html="html" v-on:click="go($event)"></div>
	</div>

</template>
<script type="text/javascript">
	import swiper from "./swiper.vue";
	export default {
		components: { swiper },
		data: function() {
			return {
				tabs: '',
				a: '1',
				html: '',
			}
		},
		mounted: function() {
			var url = "src/static/json/menu.json";
			this.$http.get(url).then(function(response) {
				this.tabs = response.body.list;
				this.html = response.body.html;
				
			}, function(response) {

			});

		},
		methods:{
			go:function(event){
				var target = event.target;
				switch(target.className){
					case "alan":
					console.log("给alan送花")
					break;
					case "k":
					console.log("我想哭");
					break;
				};
				switch(target.id){
					case "k2":
					console.log("讲真");
					break;
				};
			},
		}
	}
</script>


