<template>
	<div id="swiper">
		<div class="swiper-container">
			<div class="swiper-wrapper">
				<div class="swiper-slide" v-for="list of tabs">
					<router-link :to=list.url>
						<img src="../static/img/dis1@2x.png" />
						<p>{{list.name}}</p>
					</router-link>
				</div>
			</div>
			<div class="swiper-pagination"></div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Swiper from "../static/js/swiper.min.js";
	export default {
		props: ['tabs'],
		updated() {
			var mySwiper = new Swiper('.swiper-container', {
				autoplay: 5000, //可选选项，自动滑动
				pagination: '.swiper-pagination',
			});
		}
	}
</script>
<style lang="scss">
	@import url("../static/style/swiper.min.css");
	.swiper-slide {
		position: relative;
		width: 100%;
		height: 2rem;
		img {
			width: 100%;
			height: 100%;
		}
		p {
			position: absolute;
			height: .2rem;
			line-height: .2rem;
			text-align: center;
			font-size: .2rem;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			margin: auto;
			color: #FFFFFF;
		}
	}
</style>


